.nav {
	position: relative;
	padding-top: 19px;
	padding-bottom: 22px;
	&--mb {
		margin-bottom: 53px;
		@include md {
			margin-bottom: 0; } }
	@include md {
		position: absolute;
		top: 10px;
		left: 0px;
		padding: 0;
		margin-top: 17px; }
	@include sm {
		margin-top: 0px; }
	&__bg {
		position: absolute;
		z-index: -1;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%; }
	&__burger {
		display: none;
		position: relative;
		border-top: 2px solid $green;
		border-bottom: 2px solid $green;
		span {
			display: block;
			width: 100%;
			height: 2px;
			@include vcenter;
			background: $green; }
		@include md {
			display: block;
			width: 30px;
			height: 20px; } }


	&__inner {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include md {
			display: none;
			// display: block
			text-align: left;
			background: #fff;
			position: absolute;
			top: calc(100% + 12px);
			left: 15px;
			width: 250px;
			z-index: 10;
			padding: 15px; }
		@include mm {
			display: flex!important; } }

	a {
		display: inline-block;
		vertical-align: top;
		color: $white;
		font-size: 24px;
		transition: background 0.3s;
		padding: 3px 14px;
		border: 1px solid $white;
		border-radius: 20px;
		&.is-active {
			background: $green-d;
			@include md {
				background: none; } }
		@include lg {
			font-size: 18px; }
		@include md {
			display: block;
			border: none;
			color: $green;
			padding: 0;
			margin-bottom: 10px;
			&:last-of-type {
				margin-bottom: 0; } }

		@include mm {
			&:hover {
				background: $green-d; } } } }


