.footer {
	background: $gray2;
	padding-top: 25px;
	padding-bottom: 15px;
	font-size: 0;
	&__inner {
		@include clr; }

	&__left {
		float: left;
		margin-right: 112px;
		@include lg {
			margin-right: 50px; }
		@include md {
			margin-right: 25px; }
		@include sm {
			float: none;
			margin-right: 0;
			text-align: center; } }
	&__center {
		float: left;
		@include md {
			display: none; } }
	&__right {
		float: right;
		text-align: right;
		@include sm {
			float: none;
			text-align: center; } }
	&__logo {
		max-width: 262px;
		max-height: 45px;
		margin-bottom: 28px;
		display: inline-block;
		vertical-align: top;
		img {
			width: 100%;
			height: 100%;
			object-fit: contain; }
		@include md {
			max-width: 200px; } }
	&__nav {
		margin-bottom: 35px;
		@include md {
			margin-bottom: 10px; }
		@include sm {
			margin-bottom: 20px; }
		li {
			display: inline-block;
			vertical-align: top;
			margin-right: 45px;
			@include lg {
				margin-right: 25px; }
			@include md {
				margin-right: 15px; }
			@include sm {
				margin: 8px;
				display: block; }
			&:last-of-type {
				margin-right: 0; } }
		a {
			color: $white;
			transition: color 0.3s;
			font-size: 18px;
			@include lg {
				font-size: 16px; }
			&.is-active {
				color: $green;
				text-decoration: underline; }
			@include mm {
				&:hover {
					color: $green; } } } } }

.copyright {
	color: $black;
	font-size: 14px;
	font-weight: 400;
	@include md {
		font-size: 12px; }
	@include sm {
		display: none; }
	&--mob {
		display: none;
		@include sm {
			display: block; } } }

.accept-block {
	font-size: 0;
	p {
		color: $white;
		font-size: 18px;
		margin-bottom: 24px; }
	ul {}
	li {
		display: inline-block;
		vertical-align: top;
		margin-right: 9px;
		&:last-of-type {
			margin-right: 0; }
		img {
			width: 48px;
			height: 48px;
			@include md {
				width: 30px;
				height: 30px; } } } }
